.MainContainer{
    
    .auxiliary-header {
        display: flex;
        align-items: center;
        padding: 10px;
        background-color: #403d3d;
        
        .header-left {
          h2 {
            margin: 0;
            font-size: 20px;
            color: white;
          }
        }
      }
    .series-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
      }
      .series-tile {
        width: 200px;
        height: 200px;
        margin: 10px;
        padding: 10px;
        background-color: #f2f2f2;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        
        h3 {
          margin: 0;
          font-size: 16px;
          text-align: center;
          margin-bottom: 5px;
        }
        
        p {
          margin: 0;
          font-size: 14px;
          text-align: center;
        }
        .tile-caption {
            font-size: 12px;
            text-align: center;
            margin-top: 5px;
            color: #666;
            word-break: break-all;
          }
      }
    
     
    }